/// -----------------------------------------------------------------------------
/// This file contains all styles related to the header of the site/application.
/// -----------------------------------------------------------------------------

/// Fix affix header issue
.navbar-fixed-bottom,
.navbar-fixed-top {
    .navbar-collapse {
        max-height: none;
    }
}

/// Header
header {
    &.home-header {
        .navbar-default {
            .navbar-toggle {
                &.search-toggle-button-mobile {
                    display: none;
                }
            }
        }
    }
}

/// End Header

/// Top social
.top-social {
    background-color: $secondary-color;

    .top-header {
        height: 60px;
        display: table;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;

        .join-top {
            padding-right: 20px;
            display: table-cell;
            vertical-align: middle;
        }

        .social-icons {
            display: table-cell;
            vertical-align: middle;
        }
    }
}

/// End Top social

/// Top navbar
.top-navbar {}

/// End Top navbar

/// Navbar default
.navbar-default {
    background-color: transparent;
    border-color: inherit;
    border: none;
    border-radius: 0px;
    margin-bottom: 0px;
    min-height: 0px;

    /// Navbar header
    .navbar-header {
        background-color: white;
        height: auto;
        position: relative;

        /// Navbar brand
        .navbar-brand {
            margin-right: 15px;
            margin-left: 15px;
            margin-top: 0px;
            margin-bottom: 0px;
            max-width: 200px;
            height: auto;
            padding: 0px;

            img {
                width: 100%;
            }
        }

        /// End Navbar brand
    }

    /// End Navbar header

    /// Hamburger & Search mobile button
    .navbar-toggle {
        border: none;
        background-color: transparent;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        border-radius: 0px;

        @include on-event {
            background-color: transparent;
        }

        /// Hamburger
        &.hamburger {
            position: absolute;
            top: calc(50% - 13.5px);
            bottom: auto;
            right: 10px;
            left: auto;
            margin: 0px;
            padding: 5px;

            /// Navbar toggle opened
            .icon-bar {
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                background-color: $primary-color;
                height: 3px;
                width: 25px;

                /// Transform [X]
                &.top-bar {
                    -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
                    -webkit-transform-origin: 10% 10%;
                    transform-origin: 10% 10%;
                }

                &.middle-bar {
                    opacity: 0;
                }

                &.bottom-bar {
                    -webkit-transform: rotate(-45deg);
                    transform: rotate(-45deg);
                    -webkit-transform-origin: 10% 90%;
                    transform-origin: 10% 90%;
                }

                /// End Transform [X]
            }

            /// End Navbar toggle opened

            /// Navbar toggle closed
            &.collapsed {
                background-color: transparent;

                .icon-bar {
                    // background-color: $primary-color;

                    &.top-bar {
                        -webkit-transform: rotate(0);
                        transform: rotate(0);
                    }

                    &.middle-bar {
                        opacity: 1;
                    }

                    &.bottom-bar {
                        -webkit-transform: rotate(0);
                        transform: rotate(0);
                    }
                }
            }

            /// End Navbar toggle closed
        }

        /// End Hamburger

        /// Search mobile button
        &.search-toggle-button-mobile {
            color: $primary-color;

            position: absolute;
            top: calc(50% - 14px);
            bottom: auto;
            right: 55px;
            left: auto;
            z-index: 3;

            width: auto;
            margin: 0px;
            padding: 5px;
            font-size: 18px;
            font-weight: bold;
            line-height: 0;
            border-radius: 0px;

            /// Search mobile button closed
            &.collapsed {
                // color: $primary-color;
            }

            /// End Search mobile button closed
        }

        /// End earch mobile button
    }

    ///  End Hamburger & Search mobile button

    /// Navbar collapse -> Mobile menu dropdown
    .navbar-collapse {
        background-color: $primary-color;
        border: none;
        -webkit-box-shadow: none;
        box-shadow: none;

        /// Navigation with transition

        // height: auto !important;
        // -webkit-transition: left 0.3s ease;
        // transition: left 0.3s ease;
        // position: absolute;
        // left: 100vh;
        // right: auto;
        // width: calc(100% + 15px);
        // z-index: 3;
        // -webkit-box-shadow: -8px 8px 15px -5px rgba($black, 0.15);
        // box-shadow: -8px 8px 15px -5px rgba($black, 0.15);
        //
        // &.in{
        // 	left: 0px;
        // }

        /// End Navigation with transition


        /// Navigation overflow

        // width: 100%;
        // position: absolute;
        // right: 0px;
        // left: auto;
        // z-index: 1;
        // -webkit-box-shadow: -8px 8px 15px -5px rgba(0,0,0,0.15);
        // box-shadow: -8px 8px 15px -5px rgba(0,0,0,0.15);
        // margin: 0px;

        /// End Navigation overflow
    }

    /// End Navbar collapse -> Mobile menu dropdown

    /// Navbar nav
    ul.navbar-nav {
        margin-top: 0px;
        margin-bottom: 0px;
        text-align: center;
        background-color: transparent;

        /// Navigation items
        li a,
        .open ul.dropdown-menu>li>a,
        ul.dropdown-menu>li>a {
            color: $white;
            background-color: transparent;
            font-size: $body-font-size;
            font-weight: 500;

            line-height: 1.5;
            padding: 5px 15px;
            text-transform: none;
            word-break: break-word;
            white-space: normal;

            -webkit-transition: color 0.3s ease, background-color 0.3s ease;
            transition: color 0.3s ease, background-color 0.3s ease;

            font-weight: 400;

            @include on-event {
                color: $white;
                background-color: $third-color;
                text-decoration: none;
            }
        }

        /// End Navigation items

        /// Activated item
        li.active>a {
            color: $white;
            background-color: $hover-color;

            @include on-event {
                color: $white;
                background-color: shade($hover-color, 15%);
            }
        }

        /// End Activated item

        /// Dropdown parent item
        li.dropdown {

            /// Dropdown icon ▼
            i.fa.fa-angle-down {
                padding-left: 5px;

                &:before {
                    // content: "\f107";
                }
            }

            /// End Dropdown icon ▼

            /// Dropdown menu open
            &.open {

                /// Parent item
                >a {
                    color: $white;
                    background-color: $hover-color;
                    border-color: transparent;
                }

                /// End Parent item
            }

            /// End Dropdown menu open

            /// Dropdown menu open
            &.open,
            & {

                /// Navbar dropdown menu
                ul.dropdown-menu {
                    background-color: shade($primary-color, 15%);
                    text-align: center;
                    padding-top: 0px;
                    padding-bottom: 0px;
                    border-radius: 0px;

                    /// Custom navigation (dropdown open on hover)

                    // display: block;
                    // position: relative;
                    // width: 100%;
                    // border: none;
                    // -webkit-box-shadow: none;
                    // box-shadow: none;

                    /// End Custom navigation (dropdown open on hover)

                    /// List item
                    >li {

                        /// Dropdown menu items
                        >a {
                            color: $white;

                            @include on-event {
                                color: $white;
                                background-color: $hover-color;
                            }
                        }

                        /// End Dropdown menu items

                        /// Activated dropdown item
                        &.active>a {
                            color: $white;
                            background-color: transparent;

                            @include on-event {

                                background-color: transparent;
                            }
                        }

                        /// End Activated dropdown item
                    }

                    /// End List item
                }

                /// End Navbar dropdown menu
            }

            /// Dropdown menu open
        }

        /// End Dropdown parent item
    }

    /// Navbar nav
}

/// End Navbar default



